.breadcrumbs{
    @extend %reset-list;
    display: flex;
    align-items: center;
    min-height: 80px;
    --mainFz: 1.4rem;
    font-size: var(--mainFz);
    flex-flow: row wrap;
    @include r(767){
        white-space: nowrap;
        flex-wrap: nowrap;
        overflow-x: auto;
        min-height: 60px;
        margin-bottom: 10px;
        --mainFz: 15px;
        &__wrapper{
            position: relative;
            width: 100%;
            &::after{
                content: '';
                position: absolute;
                width: 40px;
                height: 100%;
                background-image: linear-gradient(to left, white, transparent);
                top: 0;
                right: 0;
                pointer-events: none;
            }
        }
    }
    li{
        &:not(:last-child){
            &::after{
                content: '/';
                margin: 0.5em;
                display: inline-block;
            }
        }
        &:last-child{
            color: $color-primary;
        }
    }
    a{
        color: $color-text;
        text-decoration: none;
        &:hover{
            text-decoration: underline;
        }
        span{
            color: rgba($color-text, .5);
        }
    }

}